import React, { Component } from 'react'
import { Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem, Jumbotron, Modal, Button, ModalHeader, ModalBody, FormGroup, Form,  Label, Input } from 'reactstrap';
import { NavLink } from 'react-router-dom'


class Header extends Component {
    constructor(props){
        super(props)
        this.state={
            isNavOpen: false,
            isModalOpen: false
        }
    }
    toggleNav =()=>{
            this.setState((prevState)=>({
                isNavOpen: !prevState.isNavOpen
            }))
    }
    toggleModal =()=>{
        this.setState((prevState)=>({
            isModalOpen: !prevState.isModalOpen
        }))
    }
    handleLogin=(e)=>{
        this.toggleModal()
        alert("Username: "+this.username.value + " Password: "+ this.password.value + " Remember: "+ this.remember.checked)
        e.preventDefault()
    }
    render() {
        return (
            <>
                <Navbar dark expand="md">
                    <div className="container">
                        <NavbarToggler onClick={this.toggleNav}/>
                        <NavbarBrand className="mr-auto" href="/">
                            <img src="assets/images/logo.png" height="30" width="41" alt="Ristorante Con Fusion" />
                        </NavbarBrand>
                        <Collapse isOpen={this.state.isNavOpen} navbar>
                        <Nav navbar>
                            <NavItem>
                                <NavLink className="nav-Link" to="/home">
                                    <span className="fa fa-home fa-lg"> </span> Home
                         </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-Link" to="/aboutus">
                                    <span className="fa fa-info fa-lg"> </span> About us
                         </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-Link" to="/menu">
                                    <span className="fa fa-list fa-lg"> </span> Menu
                         </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-Link" to="/contactus">
                                    <span className="fa fa-address-card fa-lg"> </span> Contact Us
                         </NavLink>
                            </NavItem>
                        </Nav>
                        <Nav className="ml-auto"navbar>
                            <NavItem>
                                <Button outline onClick={this.toggleModal}>
                                    <span className="fa fa-sign-in fa-lg"></span>
                                     Login
                                </Button>
                            </NavItem>
                        </Nav>
                        </Collapse>
                    </div>
                </Navbar>
                <Jumbotron>
                    <div className="container">
                        <div className="row row-header">
                            <div className="col-12 col-sm-6">
                                <h1>Ristorante Con Fusion</h1>
                                <p>We take inspiration from worlds best cuisines and create a unique fusion experience</p>
                            </div>
                        </div>
                    </div>
                </Jumbotron>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader>Login</ModalHeader>
                    <ModalBody>
                    <Form onSubmit={this.handleLogin}>
                        <FormGroup>
                        <Label htmlFor="username">Username</Label>
                        <Input type="text" id="username" name="username" innerRef={(input)=> this.username = input}/>
                        </FormGroup>
                        <FormGroup>
                        <Label htmlFor="password">Username</Label>
                        <Input type="password" id="password" name="password" innerRef={(input)=> this.password = input}/>
                        </FormGroup>
                        <FormGroup check>
                       <Label check>
                           <Input type="checkbox" name="remember" innerRef={(input)=> this.remember = input}/>
                           Remember me
                       </Label>
                        </FormGroup>
                        <Button type="submit" value="submit" color="primary">Login</Button>
                    </Form>
                    </ModalBody>
                </Modal>
            </>
        )
    }
}

export default Header;